"use client"

import { useEffect, useMemo, useState } from "react"
import useSWR from "swr"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import {
  BookOpen,
  CheckCircle2,
  Circle,
  Lock,
  Clock,
  Target,
  ArrowRight,
  TrendingUp,
  Calendar,
  Bell,
  Sparkles,
  Layers,
  Shield,
  ChevronRight,
} from "lucide-react"
import Link from "next/link"
import { useRouter, useSearchParams } from "next/navigation"
import { enrollmentsApi } from "@/services/api"
import { cn } from "@/lib/utils"
import type { Phase } from "@/types/enrollment"

const proofPointStatusThemes = {
  completed: {
    label: "Completado",
    badge: "bg-emerald-50 text-emerald-600 border border-emerald-100",
    card: "border-emerald-100 shadow-[0_10px_30px_rgba(16,185,129,0.12)]",
  },
  in_progress: {
    label: "En progreso",
    badge: "bg-primary/10 text-primary border border-primary/30",
    card: "border-primary/30 shadow-[0_12px_30px_rgba(14,165,233,0.18)]",
  },
  available: {
    label: "Disponible",
    badge: "bg-sky-50 text-sky-600 border border-sky-100",
    card: "border-sky-100 shadow-[0_10px_25px_rgba(14,165,233,0.12)]",
  },
  locked: {
    label: "Bloqueado",
    badge: "bg-slate-100 text-slate-500 border border-slate-200",
    card: "border-dashed border-slate-200 bg-slate-50/70",
  },
} as const

const phaseStatusThemes = {
  completed: "bg-emerald-50 text-emerald-600 border-emerald-100",
  in_progress: "bg-primary/10 text-primary border-primary/30",
  available: "bg-sky-50 text-sky-600 border-sky-100",
  locked: "bg-slate-100 text-slate-500 border-slate-200",
} as const

const phaseStatusLabels = {
  completed: "Completada",
  in_progress: "En curso",
  available: "Disponible",
  locked: "Bloqueada",
} as const

const getPhaseProgress = (phase?: Phase) => {
  if (!phase || phase.proofPoints.length === 0) return 0
  const total = phase.proofPoints.reduce((acc, proofPoint) => acc + (proofPoint.progress ?? 0), 0)
  return Math.round(total / phase.proofPoints.length)
}

const getPhaseStatus = (phase?: Phase): keyof typeof phaseStatusThemes => {
  if (!phase || phase.proofPoints.length === 0) return "locked"
  const statuses = phase.proofPoints.map((proofPoint) => proofPoint.status)
  if (statuses.every((status) => status === "completed")) return "completed"
  if (statuses.some((status) => status === "in_progress")) return "in_progress"
  if (statuses.some((status) => status === "available")) return "available"
  return "locked"
}

export default function DashboardPage() {
  const router = useRouter()
  const searchParams = useSearchParams()

  const studentId =
    searchParams.get("studentId") ??
    process.env.NEXT_PUBLIC_STUDENT_ID ??
    process.env.NEXT_PUBLIC_DEFAULT_STUDENT_ID ??
    undefined
  const [selectedPhaseIdx, setSelectedPhaseIdx] = useState(0)

  const {
    data: enrollments,
    isLoading: loadingEnrollments,
  } = useSWR(
    ["my-enrollments", studentId ?? "default-student"],
    () => enrollmentsApi.getMy(studentId)
  )

  const activeEnrollment = enrollments?.[0]

  const { data: structure } = useSWR(
    activeEnrollment ? ["enrollment-structure", activeEnrollment.id] : null,
    () => enrollmentsApi.getStructure(activeEnrollment.id)
  )

  const { data: continuePoint } = useSWR(
    activeEnrollment ? ["continue-point", activeEnrollment.id] : null,
    () => enrollmentsApi.getContinuePoint(activeEnrollment.id)
  )

  const phases = structure?.phases ?? []
  const selectedPhase = phases[selectedPhaseIdx]

  useEffect(() => {
    if (phases.length > 0 && selectedPhaseIdx >= phases.length) {
      setSelectedPhaseIdx(0)
    }
  }, [phases.length, selectedPhaseIdx])

  const formatDate = (value?: string | Date | null) => {
    if (!value) return null
    const parsed = typeof value === "string" ? new Date(value) : value
    if (Number.isNaN(parsed.getTime())) return null
    return parsed.toLocaleDateString("es-ES", {
      month: "short",
      day: "numeric",
    })
  }

  const stats = useMemo(() => {
    if (!activeEnrollment) {
      return { progress: 0, proofPointsLabel: "0/0", completed: 0, total: 0 }
    }
    const completed = activeEnrollment.completedProofPoints ?? 0
    const total = activeEnrollment.totalProofPoints ?? 0
    return {
      progress: activeEnrollment.overallProgress ?? 0,
      proofPointsLabel: `${completed}/${total}`,
      completed,
      total,
      instructor: activeEnrollment.instructorName,
      cohort: activeEnrollment.cohortId,
    }
  }, [activeEnrollment])

  const selectedPhaseProgress = getPhaseProgress(selectedPhase)
  const selectedPhaseStatus = getPhaseStatus(selectedPhase)
  const estimatedCompletionLabel =
    formatDate(activeEnrollment.estimatedCompletionDate ?? null) ?? "En progreso"

  if (loadingEnrollments) {
    return (
      <div className="flex min-h-screen items-center justify-center text-muted-foreground">
        Cargando tu espacio de aprendizaje...
      </div>
    )
  }

  if (!activeEnrollment) {
    return (
      <div className="flex min-h-screen flex-col items-center justify-center gap-4 px-4 text-center">
        <BookOpen className="h-12 w-12 text-primary" />
        <div>
          <h1 className="text-2xl font-semibold">Aún no tienes un programa asignado</h1>
          <p className="text-muted-foreground">
            Solicita a tu instructor que te inscriba en una cohorte para comenzar.
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="flex min-h-screen flex-col bg-slate-50">
      <header className="border-b bg-white/80 backdrop-blur supports-[backdrop-filter]:bg-white/60">
        <div className="mx-auto flex h-16 w-full max-w-7xl items-center justify-between px-6">
          <div className="flex items-center gap-3">
            <div className="flex h-10 w-10 items-center justify-center rounded-2xl bg-primary text-sm font-semibold uppercase text-white">
              X
            </div>
            <div>
              <p className="text-xs font-medium uppercase tracking-[0.12em] text-muted-foreground">
                Xpertia
              </p>
              <p className="text-sm font-semibold text-foreground">Espacio del estudiante</p>
            </div>
          </div>
          <nav className="hidden items-center gap-6 text-sm text-muted-foreground md:flex">
            <button className="font-semibold text-foreground" type="button">
              Programas
            </button>
            <button className="transition hover:text-foreground" type="button">
              Roadmap
            </button>
            <button className="transition hover:text-foreground" type="button">
              Biblioteca
            </button>
          </nav>
          <div className="flex items-center gap-2">
            <Button variant="ghost" size="icon" className="text-muted-foreground">
              <Bell className="h-4 w-4" />
            </Button>
            <Button variant="outline" size="sm" asChild>
              <Link href="/dashboard/progress">Mi progreso</Link>
            </Button>
            <Avatar>
              <AvatarFallback>ES</AvatarFallback>
            </Avatar>
          </div>
        </div>
      </header>

      <main className="mx-auto flex w-full max-w-7xl flex-1 flex-col gap-8 px-6 py-8">
        <section className="grid gap-6 lg:grid-cols-[1.5fr,1fr]">
          <Card className="relative overflow-hidden border-none bg-gradient-to-r from-primary/10 via-primary/5 to-white shadow-xl">
            <CardContent className="relative z-10 flex flex-col gap-6 p-6 text-slate-900 md:flex-row md:items-center md:justify-between">
              <div className="space-y-3">
                <Badge
                  variant="outline"
                  className="border-white/60 bg-white/70 text-xs font-semibold uppercase tracking-wide text-slate-600"
                >
                  Programa activo
                </Badge>
                <h1 className="text-3xl font-semibold leading-tight">
                  {activeEnrollment.programName}
                </h1>
                <p className="text-base text-slate-600">
                  {activeEnrollment.programDescription}
                </p>
                <div className="flex flex-wrap items-center gap-4 text-sm text-slate-600">
                  <span className="flex items-center gap-2">
                    <Layers className="h-4 w-4 text-primary" />
                    Fase {selectedPhase ? selectedPhase.orden : "—"}
                  </span>
                  <span className="flex items-center gap-2">
                    <Calendar className="h-4 w-4 text-primary" />
                    {estimatedCompletionLabel}
                  </span>
                  <span className="flex items-center gap-2">
                    <Clock className="h-4 w-4 text-primary" />
                    {selectedPhase?.duracionSemanas
                      ? `${selectedPhase.duracionSemanas} semanas`
                      : "Duración estimada"}
                  </span>
                </div>
              </div>
              <div className="w-full max-w-sm rounded-2xl bg-white/80 p-5 shadow-lg backdrop-blur">
                <p className="text-sm font-medium text-muted-foreground">Avance general</p>
                <div className="mt-2 flex items-baseline gap-2">
                  <span className="text-4xl font-semibold text-primary">{stats.progress}%</span>
                  <span className="text-sm text-muted-foreground">
                    {stats.proofPointsLabel} proof points
                  </span>
                </div>
                <Progress value={stats.progress} className="mt-3 h-2" />
                <Button
                  className="mt-4 w-full"
                  onClick={() =>
                    continuePoint
                      ? router.push(`/exercises/${continuePoint.exerciseId}`)
                      : router.push("/dashboard/progress")
                  }
                >
                  {continuePoint ? "Continuar donde te quedaste" : "Ver mi progreso"}
                  <ArrowRight className="ml-2 h-4 w-4" />
                </Button>
              </div>
            </CardContent>
            <div className="pointer-events-none absolute inset-0 bg-[radial-gradient(circle_at_top,_rgba(255,255,255,0.9),transparent_55%)]" />
          </Card>

          <Card className="border-none bg-white shadow-lg">
            <CardHeader className="pb-2">
              <div className="flex items-center gap-2">
                <Sparkles className="h-5 w-5 text-primary" />
                <CardTitle className="text-base font-semibold">
                  {continuePoint ? "Retoma tu siguiente ejercicio" : "Prepárate para el próximo reto"}
                </CardTitle>
              </div>
            </CardHeader>
            <CardContent className="space-y-4">
              {continuePoint ? (
                <>
                  <p className="text-sm text-muted-foreground">{continuePoint.proofPointName}</p>
                  <h3 className="text-xl font-semibold leading-tight">{continuePoint.exerciseName}</h3>
                  <div className="flex flex-wrap gap-4 text-sm text-muted-foreground">
                    <span className="flex items-center gap-1">
                      <Clock className="h-4 w-4" />
                      {continuePoint.estimatedTimeRemaining} min restantes
                    </span>
                    <span className="flex items-center gap-1">
                      <Target className="h-4 w-4" />
                      {Math.round(continuePoint.progress ?? 0)}% completado
                    </span>
                  </div>
                  <Progress value={continuePoint.progress ?? 0} className="h-2" />
                  <Button
                    className="w-full"
                    onClick={() => router.push(`/exercises/${continuePoint.exerciseId}`)}
                  >
                    Continuar ahora
                    <ArrowRight className="ml-2 h-4 w-4" />
                  </Button>
                </>
              ) : (
                <div className="space-y-3 text-sm text-muted-foreground">
                  <p>
                    Revisa tus proof points activos y comienza el siguiente ejercicio recomendado
                    para mantener el ritmo.
                  </p>
                  <Button className="w-full" variant="outline" asChild>
                    <Link href="/dashboard/progress">Explorar roadmap</Link>
                  </Button>
                </div>
              )}
            </CardContent>
          </Card>
        </section>

        <section className="grid gap-4 md:grid-cols-2 xl:grid-cols-4">
          <Card className="border-none bg-white shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between pb-3">
              <div>
                <p className="text-sm text-muted-foreground">Progreso general</p>
                <p className="text-3xl font-semibold text-primary">{stats.progress}%</p>
              </div>
              <TrendingUp className="h-9 w-9 text-primary/80" />
            </CardHeader>
            <CardContent>
              <Progress value={stats.progress} className="h-2" />
              <p className="mt-2 text-xs text-muted-foreground">
                {stats.proofPointsLabel} proof points completados
              </p>
            </CardContent>
          </Card>
          <Card className="border-none bg-white shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between pb-3">
              <div>
                <p className="text-sm text-muted-foreground">Tu instructor</p>
                <p className="text-xl font-semibold">{stats.instructor}</p>
                <p className="text-xs text-muted-foreground">Cohorte {stats.cohort}</p>
              </div>
              <Shield className="h-8 w-8 text-sky-500" />
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Agenda mentorías o resuelve dudas desde tu panel.
              </p>
            </CardContent>
          </Card>
          <Card className="border-none bg-white shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between pb-3">
              <div>
                <p className="text-sm text-muted-foreground">Proof points</p>
                <p className="text-3xl font-semibold">
                  {stats.completed}/{stats.total}
                </p>
                <p className="text-xs text-muted-foreground">Finalizados vs totales</p>
              </div>
              <BookOpen className="h-9 w-9 text-indigo-500" />
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Sigue desbloqueando niveles para avanzar en tu ruta.
              </p>
            </CardContent>
          </Card>
          <Card className="border-none bg-white shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between pb-3">
              <div>
                <p className="text-sm text-muted-foreground">Fase destacada</p>
                <p className="text-xl font-semibold">
                  {selectedPhase ? selectedPhase.nombre : "Sin asignar"}
                </p>
              </div>
              {selectedPhase && (
                <Badge
                  variant="outline"
                  className={cn(
                    "rounded-full px-3 py-1 text-xs font-medium",
                    phaseStatusThemes[selectedPhaseStatus]
                  )}
                >
                  {phaseStatusLabels[selectedPhaseStatus]}
                </Badge>
              )}
            </CardHeader>
            <CardContent>
              <div className="flex items-center gap-3">
                <Progress value={selectedPhaseProgress} className="h-2 flex-1" />
                <span className="text-sm font-semibold">{selectedPhaseProgress}%</span>
              </div>
              <p className="mt-2 text-xs text-muted-foreground">
                {selectedPhase?.duracionSemanas
                  ? `${selectedPhase.duracionSemanas} semanas estimadas`
                  : "Duración estimada por definir"}
              </p>
            </CardContent>
          </Card>
        </section>

        <section className="grid gap-6 lg:grid-cols-[360px,1fr]">
          <Card className="border-none bg-white shadow-lg">
            <CardHeader className="pb-4">
              <CardTitle>Ruta del programa</CardTitle>
              <p className="text-sm text-muted-foreground">
                Explora las fases y selecciona dónde quieres enfocarte hoy.
              </p>
            </CardHeader>
            <CardContent className="space-y-3">
              {phases.length === 0 && (
                <p className="text-sm text-muted-foreground">
                  Esta cohorte aún no tiene estructura publicada.
                </p>
              )}
              {phases.map((phase, idx) => {
                const status = getPhaseStatus(phase)
                const progress = getPhaseProgress(phase)
                return (
                  <button
                    key={phase.id}
                    type="button"
                    onClick={() => setSelectedPhaseIdx(idx)}
                    className={cn(
                      "w-full rounded-2xl border border-transparent bg-white/70 p-4 text-left transition-all hover:border-primary/40 hover:bg-white",
                      selectedPhaseIdx === idx && "border-primary/40 bg-white shadow-md"
                    )}
                  >
                    <div className="flex items-start justify-between gap-3">
                      <div>
                        <p className="text-xs uppercase text-muted-foreground">
                          Fase {phase.orden}
                        </p>
                        <p className="text-base font-semibold text-foreground">{phase.nombre}</p>
                      </div>
                      <Badge
                        variant="outline"
                        className={cn(
                          "rounded-full px-3 py-1 text-xs font-medium",
                          phaseStatusThemes[status]
                        )}
                      >
                        {phaseStatusLabels[status]}
                      </Badge>
                    </div>
                    <p className="mt-2 text-sm text-muted-foreground">
                      {phase.descripcion || "Próximamente más detalles"}
                    </p>
                    <div className="mt-3 flex items-center gap-2">
                      <Progress value={progress} className="h-2 flex-1" />
                      <span className="text-sm font-semibold">{progress}%</span>
                    </div>
                  </button>
                )
              })}
            </CardContent>
          </Card>

          <Card className="border-none bg-white shadow-lg">
            <CardHeader className="flex flex-col gap-1 pb-4">
              <div className="flex items-center justify-between gap-4">
                <div>
                  <CardTitle className="text-xl">
                    {selectedPhase ? selectedPhase.nombre : "Selecciona una fase"}
                  </CardTitle>
                  <p className="text-sm text-muted-foreground">
                    Conoce el detalle de los proof points y retoma tus ejercicios.
                  </p>
                </div>
                <Button variant="ghost" size="sm" asChild>
                  <Link href="/dashboard/progress">
                    Ver progreso
                    <ChevronRight className="ml-1 h-4 w-4" />
                  </Link>
                </Button>
              </div>
            </CardHeader>
            <CardContent>
              {!selectedPhase && (
                <p className="text-sm text-muted-foreground">
                  Selecciona una fase para revisar sus proof points.
                </p>
              )}
              {selectedPhase && selectedPhase.proofPoints.length === 0 && (
                <p className="text-sm text-muted-foreground">
                  Esta fase aún no tiene proof points publicados.
                </p>
              )}
              {selectedPhase && selectedPhase.proofPoints.length > 0 && (
                <div className="grid gap-4 md:grid-cols-2">
                  {selectedPhase.proofPoints.map((proofPoint) => {
                    const theme = proofPointStatusThemes[proofPoint.status]
                    return (
                      <div
                        key={proofPoint.id}
                        className={cn(
                          "flex flex-col rounded-2xl border bg-white p-4 transition-all hover:-translate-y-0.5",
                          theme.card
                        )}
                      >
                        <div className="flex items-center justify-between gap-2">
                          <Badge
                            variant="outline"
                            className={cn(
                              "rounded-full px-3 py-1 text-xs font-medium",
                              theme.badge
                            )}
                          >
                            {theme.label}
                          </Badge>
                          <span className="text-xs text-muted-foreground">
                            {proofPoint.exercises.length} ejercicios
                          </span>
                        </div>
                        <h3 className="mt-3 text-lg font-semibold text-foreground">
                          {proofPoint.nombre}
                        </h3>
                        <p className="mt-1 text-sm text-muted-foreground">
                          {proofPoint.preguntaCentral || proofPoint.descripcion}
                        </p>
                        <div className="mt-4 flex items-center gap-2">
                          <Progress value={proofPoint.progress} className="h-2 flex-1" />
                          <span className="text-sm font-semibold">
                            {Math.round(proofPoint.progress)}%
                          </span>
                        </div>
                        <Button
                          className="mt-4"
                          size="sm"
                          variant={proofPoint.status === "locked" ? "secondary" : "default"}
                          disabled={proofPoint.status === "locked"}
                          onClick={() => router.push(`/proof-points/${proofPoint.id}`)}
                        >
                          {proofPoint.status === "completed"
                            ? "Revisar"
                            : proofPoint.status === "locked"
                              ? "Bloqueado"
                              : "Continuar"}
                          <ArrowRight className="ml-2 h-4 w-4" />
                        </Button>
                      </div>
                    )
                  })}
                </div>
              )}
            </CardContent>
          </Card>
        </section>
      </main>
    </div>
  )
}
